/**
 * Effet model events
 */

'use strict';

import {EventEmitter} from 'events';
import Effet from './effet.model';
var EffetEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
EffetEvents.setMaxListeners(0);

// Model events
var events = {
  'save': 'save',
  'remove': 'remove'
};

// Register the event emitter to the model events
for (var e in events) {
  var event = events[e];
  Effet.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    EffetEvents.emit(event + ':' + doc._id, doc);
    EffetEvents.emit(event, doc);
  }
}

export default EffetEvents;
